@keyframes spinner {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}

.loading-spinner {
    width: 50px;
    height: 50px;
    border: 10px solid var(--secondary-color);
    border-top: 10px solid var(--primary-color);
    border-radius: 50%;
    animation: spinner 1.5s linear infinite;
}
